import { SearchIcon } from "@heroicons/react/solid";
import { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";
import { TicketPriority, TicketStatus } from "../../models/enums";
import { TicketParams } from "../../models/ticketParams";
import { EnumToArray } from "../../util/enumToArray";
import Dropdown from "../common/Dropdown";

interface Props {
  params: TicketParams;
  setParams: (value: any) => void;
}

export default function TicketsFilters({ params, setParams }: Props) {
  const [searchTerm, setSearchTerm] = useState(params.searchTerm);
  const [searchParams] = useSearchParams();
  const startDate = searchParams.get("startDate");
  const endDate = searchParams.get("endDate");

  const handleSearch = (event: any) => {
    if (searchTerm !== params.searchTerm) setParams({ searchTerm });
  };

  const setStatus = (event: any) => {
    const value = event.target.value;
    if (params.status !== value)
      setParams({ status: value === "-1" ? null : value });
  };

  const setPriority = (event: any) => {
    const value = event.target.value;
    if (params.priority !== value)
      setParams({ priority: value === "-1" ? null : value });
  };

  useEffect(() => {
    if (startDate || endDate) {
      setParams({ startDate, endDate });
    }
  }, [endDate, setParams, startDate]);

  return (
    <div className="w-full flex flex-row  justify-around items-center  py-10">
      <div className=" w-full max-w-xs flex flex-col ">
        <div className="flex flex-row gap-x-3 ">
          <input
            autoComplete="off"
            placeholder="Search for project"
            className="border-x-2 py-2 pt-3 block px-5 placeholder:font-Oswald placeholder:text-gray-300 w-full h-full   font-Montserrat font-thin text-2xl text-white bg-slate-500 bg-opacity-50  focus-within:outline-none border-x-white"
            aria-label="Search"
            type="text"
            aria-placeholder="Search"
            onChange={(event: any) => {
              setSearchTerm(event.target.value);
            }}
          />
          <button title="Search Button" onClick={handleSearch}>
            <SearchIcon className="w-8 h-8" />
          </button>
        </div>
      </div>

      <Dropdown
        title="Status"
        selectedValue={"All"}
        onChange={(value) =>
          setParams({ status: value === "-1" ? null : value })
        }
        items={[{ name: "All", value: "-1" }, ...EnumToArray(TicketStatus)]}
      />

      <Dropdown
        title="Priority"
        selectedValue={"All"}
        onChange={(value) =>
          setParams({ priority: value === "-1" ? null : value })
        }
        items={[{ name: "All", value: "-1" }, ...EnumToArray(TicketPriority)]}
      />
    </div>
  );
}

const ticketStatusOptions = [
  { name: "All", value: -1 },
  { name: "New", value: 0 },
  { name: "Assigned", value: 1 },
  { name: "Open", value: 2 },
  { name: "Fixed", value: 3 },
  { name: "Pending", value: 4 },
  { name: "Retest", value: 5 },
  { name: "Verified", value: 6 },
  { name: "Reopen", value: 7 },
  { name: "Closed", value: 8 },
  { name: "Duplicate", value: 9 },
  { name: "Rejected", value: 10 },
  { name: "Deferred", value: 11 },
  { name: "NotBug", value: 12 },
];

const ticketPriorityOptions = [
  { name: "All", value: -1 },
  { name: "Low", value: 0 },
  { name: "Medium", value: 1 },
  { name: "High", value: 2 },
  { name: "Urgent", value: 3 },
];
